/* --------- SPEECHBUBBLES --------- */
.source-reader {
    color: grey;
    font-size: 10px;
    text-transform: uppercase;
    margin-top: -9.1px;
    margin-bottom: -8px;
}

.speechbubble-reader {
    position: relative;
    display: block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    max-width: auto;
	border-radius: .43em;
    padding: 6px 13px;
    /* margin: 2% 2.8% 2% 2%; */
    -webkit-box-shadow: 2px 4px 2px 0px rgba(0,0,0,0.28);
            box-shadow: 2px 4px 2px 0px rgba(0,0,0,0.28);
}

.speechbubble-reader:after {
    content: '';
    position: absolute;
	top: 0;
	left: 0%;
	width: 0;
	height: 0;
	border: 10px solid transparent;
	border-top: 0;
	margin-left: 5px;
    margin-top: -6px;
}

.red-reader {
    background: #ffadad;
    color: #d60000;
}

.red-reader:after {
    border-bottom-color: #ffadad;
}

.green-reader {
    background: #b9f8b9;
    color: #006400;
}

.green-reader:after {
    border-bottom-color: #b9f8b9;
}

.blue-reader {
    background: rgb(142, 227, 255);
    color: darkblue;
}

.blue-reader:after {
    border-bottom-color: rgb(142, 227, 255);
}

.black-reader {
    width: 95%;
}

.narrator-reader {
    display: none;
}

.quotation {
    display: none;
}

#quote {
    margin-left: 1em;
    padding-left: 1em;
    text-indent: -1em;
    display: inline-block;
}


